export class Treaty {
  pageTitle: string;
  treatyTitle: string;
  registrationNumber: string;
  conclusionDate: string;
  mTDSGNumber: string;
  treatyItems: TreatyItem[];

  constructor() {
    this.pageTitle = null;
    this.treatyTitle = null;
    this.treatyTitle = null;
    this.registrationNumber = null;
    this.conclusionDate = null;
    this.mTDSGNumber = null;
    this.treatyItems = [];
  }

}

export class TreatyItem {
  participant: string;
  englishTitle: string;
  modifier: string;
  dataOfModification: string;
  webPublished: string;
}

